footer {
  background-color: var(--color-charcoal-1);
  padding-top:3rem;
  padding-bottom:3rem;
  display:flex;
  justify-content:center;




  .upper {
    grid-column: 1/ span 12;
    border: .125rem solid #fffa0c;
    border-top: 0;
    border-left: 0;
    border-right:0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 1rem;

    .left {
      align-self: end;

      svg {
        fill: #fffa0c;
        width: 300px;
      }

    }

    .right {
      text-align: right;
      padding-right: 2rem;

      h6 {
        margin-bottom: 1rem;
      }

      a {

        color: var(--color-charcoal-7);

        &:hover {
          cursor: pointer;
          color: $color-sunshine;

        }
      }

      .social {

        margin-top: 3rem;

        ul {
          list-style: none;

          a:nth-last-child(1) > li {
            margin-right: 0;
          }

          li {
            display: inline-block;
            margin-right: 0.125rem;

            svg {
              fill: #fffa0c;
              cursor: pointer;
              transition: ease 0.5s;

              &:hover {
                fill: #fff;
                transform: scale(1.2);
                transition: ease 0.3s;
              }
            }
          }
        }

        svg {
          fill: #fffa0c;
          width: 35px;

        }
      }

      p {
        color: $color-sunshine;
      }

    }
    @include breakpoint(w-med) {
      grid-template-columns: 1fr;
      border-radius: 0 0 1.5rem 0;

      .left {

        margin-bottom: 2rem;

        svg {
          width: 200px;
        }
      }

      .right {
        padding-right: 0.25rem;

        h6 {
          text-align: left;
        }
      }
    }

  }

  .lower {
    grid-row:2;
  
    display: grid;
    grid-auto-flow: column;
    column-gap: 1.5rem;
    h6 {
      margin-top: 1rem;
      color: var(--color-charcoal-7);
    }
  }
}
