.press-table {
    @extend %grid;
    

    &__press-link{
        grid-column: span 12;
        border-width: 0 0 1px 0;
        border-style: solid;
        border-color:var(--color-charcoal-5);
    display:grid;
    grid-template-columns: 70% auto;
    grid-column-gap: 2rem;
    padding-bottom: 3rem;
    padding-top: 3rem;

    @include breakpoint(w-sm) {
        grid-template-columns: 1fr 1fr;
    }
    
    

    }

    &__title {
        @include breakpoint(w-sm) {
        grid-column-end: span 2;
        }
    }

    p {
        color:var(--color-charcoal-5);
        font-weight:700;
        margin-bottom:1rem;
    }

    h5 {
        color:var(--color-charcoal-1);
    }

    &__pub-logo {
        align-self: center;
        padding: 0rem 3rem;
        margin-top: 1rem;
        margin-bottom: 1rem;
        justify-self:center;
        img {
            height:4rem;
            min-width: 3rem;
            max-width: 11rem;
        }

    @include breakpoint(w-sm) {
        
        padding: 1.5rem 0rem 0rem;
        grid-column:2;

        img {
            height:3rem;
            min-width: 3rem;
            max-width: 11rem;
        }

    }

    }


}